import {
  Button,
  Typography,
} from "@material-ui/core";
import { DataGrid } from "@material-ui/data-grid";
import { Fragment, useEffect, useState } from "react";
import { useAlert } from "react-alert";
import { MdDelete, MdStar } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import {
  clearErrors,
  deleteReview,
  getAllReviews,
} from "../../../../redux/actions/productAction";
import {
  DELETE_REVIEW_RESET,
  GET_ALL_REVIEWS_RESET,
} from "../../../../redux/constants/productConstant";
import { adminAuth, userAuth } from "../../../../util/userAuth";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import "./Reviews.css";

const Reviews = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();

  const { isAuthenticated, user } = useSelector((state) => state.user);
  const { error: errorReviews, reviews } = useSelector(
    (state) => state.getAllReviews
  );
  const { error, isDeleted } = useSelector((state) => state.review);

  const [productId, setProductId] = useState("");
 

  const ReviewDeleteHandler = (id) => {
    dispatch(deleteReview(id, productId));
   
  };

  const columns = [
    { field: "id", headerName: "Review Id", flex: 0.7,minWidth:180 },
    {
      field: "user",
      headerName: "User",
      flex: 0.5,
      minWidth:150 
    },
    {
      field: "comment",
      headerName: "Comment",
      flex: 1,
      minWidth:250 
    },
    {
      field: "rating",
      headerName: "Rating",
      type: "number",
      flex: 0.4,
      minWidth:130 ,
      cellClassName: (params) => {
        return params.getValue(params.id, "rating") > 3
          ? "greenColor"
          : "redColor";
      },
    },

    {
      field: "actions",
      headerName: "Actions",
      flex: 0.4,
      minWidth:130 ,
      type: "number",
      sortable: false,
      renderCell: (params) => {
        return (
          <Fragment>
            <Button onClick={() =>
                    ReviewDeleteHandler(params.getValue(params.id, "id"))}>
              <MdDelete />
            </Button>
         
          </Fragment>
        );
      },
    },
  ];

  const rows = [];
  reviews &&
    reviews.length > 0 &&
    reviews.forEach((review) => {
      rows.push({
        id: review._id,
        user: review.name,
        comment: review.comment,
        rating: review.rating,
      });
    });


useEffect(() => {
    userAuth(isAuthenticated, navigate);
    adminAuth(true, user, navigate, alert);
    dispatch({ type: GET_ALL_REVIEWS_RESET });
    
    if (productId.length === 24) {
      dispatch(getAllReviews(productId));
    }

    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    if (errorReviews) {
      alert.error(errorReviews);
      dispatch(clearErrors());
    }

    if (isDeleted) {
      alert.success("Review Deleted Successfully");
      getAllReviews(productId);
      dispatch({ type: DELETE_REVIEW_RESET });
    }
  }, [
    isAuthenticated,
    navigate,
    alert,
    productId,
    user,
    dispatch,
    error,
    errorReviews,
    isDeleted,
  ]);

  return (
    <Fragment>
      <MetaData title="All Reviews - Admin" />

      <div className="dashboard">
        <SideBar />
        <div>
          <form>
            <Typography className="ReviewFormHeading">All Reviews</Typography>
            <div>
              <MdStar />
              <input
                type="text"
                placeholder="Product Id"
                required
                value={productId}
                onChange={(e) => setProductId(e.target.value)}
              />
            </div>
          </form>
          <div>
            {reviews && reviews.length > 0 ? (
              <DataGrid
                columns={columns}
                rows={rows}
                pageSize={10}
                disableSelectionOnClick
                className="ReviewTable"
                autoHeight
              />
            ):<div className="noReview">No Reviews Found</div>}
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default Reviews;
